.App {
  text-align: center;
}
#root {
  margin-left: 4px;
  margin-right: 4px;
  margin-bottom: 4px;
}
.song-content {
  white-space: pre-wrap;
  margin: 0px 0px 0px 0px;
  min-height: 1.25em !important; /* min-height value is hard-coded in Auto-scroll logic for correct text-height calculation! */
}
.absolute-center {
  top: 50%;
  left: 50%;
  margin-right: -50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  position: absolute;
}

